@font-face {
  font-family: foo;
  src: url(arm.ttf);
  font-size: 16px;
  font-weight: unset;
}
body, html, .body, .bgg {
  font-family: foo;
  -webkit-font-smoothing: antialiased;
-moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  overflow: hidden;
  height: 100%;
  width: 100%;
  color: #fff;
}
*{
  user-select: none;
}
.bgg {
  position: absolute;
  background:  url(https://picsum.photos/1280/720) no-repeat center ;
  background-size: cover;
  transition: 0.5s linear;
  filter: blur(5px);
  z-index: -1;
}

.borx {
  background: #00000069;
  padding: 1rem;
  box-shadow: 14px 0px 2px #00000069, -14px 0px 2px #00000069, 0 14px 2px #00000069, 0 -14px 2px #00000069, 2px 2px 2px #222222a6, -2px -2px 2px #222222a6, 0 10px 20px 9px #222222a6;
}
.time {
  font-size: -webkit-xxx-large;
}
#container>div {
  margin: 35px;
}
#container {
  padding: 4rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;
  align-content: center;
  align-items: center;
}
.todo {
  height: 50vh;
  width: 50%;
  position: relative;
}
input[type="text"] {
  width: 93%;
  outline: none;
  border: none;
  border-radius: 20px;
  padding: 1rem;
  box-shadow: 0px 0px 11px #6ab0df;
  font-size: inherit;
}
input[type="text"] {
  width: 88%;
  outline: none;
  border: none;
  border-radius: 20px;
  padding: 1rem;
  box-shadow: 0px 0px 11px #6ab0df;
  font-size: inherit;
}
.add {
  position: absolute;
  bottom: 13px;
  width: 100%;
  left: 0;
  text-align: center;
}
.title {
  text-align: center;
}
.task {
  position: relative;
  margin: 6px;
}

.work {
  padding: 0.1rem 1rem;
}
.task {
  position: relative;
  margin: 6px;
  display: flex;
  letter-spacing: 2px;
  font-size: 20px;
}

.task:after {content: '';position: absolute;height: 10px;width: 10px;top: 6px;left: 0;}

.work {
  padding: 0.1rem 1rem;
}

.task.done:after {
  background: radial-gradient(#8BC34A, transparent);
}

.task.undone:after {
  background: radial-gradient(#F44336, transparent);
}
.task.done {
  text-decoration: line-through;
}
.remove{
  opacity: 0;
  transition: 0.9s linear;
  background: url(./rm.png);
  height: 20px;
  width: 20px;
  background-size: contain;
}
.task:hover .remove {
  opacity: 1;
}

.task .remove:hover {
  opacity: 1;
}
.list {
  height: 33vh;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, .2)
}

::-webkit-scrollbar-track {
  background: hsla(0, 0%, 100%, .08)
}
